/**
 * Bio component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Image from "gatsby-image"

import { rhythm } from "../utils/typography"

const PostFooter = (props) => {
  const data = useStaticQuery(graphql`
    query BioQuery {
      avatar: file(absolutePath: { regex: "/profile-pic.jpg/" }) {
        childImageSharp {
          fixed(width: 50, height: 50) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      site {
        siteMetadata {
          author
          social {
            twitter
          }
        }
      }
    }
  `)

  const { author, social } = data.site.siteMetadata
  return (
    <div
      style={{
        display: `flex`,
        marginBottom: rhythm(2.5),
      }}
    >
      <Image
        fixed={data.avatar.childImageSharp.fixed}
        alt={author}
        style={{
          marginRight: rhythm(1 / 2),
          marginBottom: 0,
          minWidth: 50,
          borderRadius: `100%`,
        }}
        imgStyle={{
          borderRadius: `50%`,
        }}
      />
      <p className="PostFooterBody">
        Thank you very much for reading! Got a comment? Tweet &nbsp;
        <a href={`https://twitter.com/${social.twitter}`} rel="noopener noreferrer" target="_blank">
           @croloris
        </a> 
        . 
        <br/>
        
        { 
          !props.notwitch && (
          <p style={{marginTop: 5, fontWeight: 600}}>
            P.S. I started &nbsp; 
            <a href="https://twitch.tv/kristoff_it" rel="noopener noreferrer" target="_blank">
              live coding regularly on Twitch
            </a>, 
              follow the channel to be notified when I go live next!
          </p>)
        }
         
      </p>
    </div>
  )
}

export default PostFooter
